.bottom {
    background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
    height: 15vh;
    padding-top: 0.5vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    color: #bbb;
    gap: 2vh
  }

.team {
    display: flex;
    gap: 10vw;
    text-align: center;
    font-size: 0.9rem;
    letter-spacing: 5px;
}

 .production {
    display: flex;
    gap: 1vw;
    text-align: center;
    font-size: 0.8rem;
    letter-spacing: 5px;
 }

.legal {
    display: flex;
    gap: 1vw;
    text-align: center;
    font-size: 0.7rem;
    letter-spacing: 5px;
}

.legal-link{
  outline: none;
  color: inherit;
  text-decoration: none;
  padding: 0px;
}