  .countdown-wrapper {
    width: 100%;
    background: linear-gradient(90deg, rgb(38, 37, 37) 0%, rgb(36, 33, 33) 100%);
    box-shadow: 0px 5px 5px rgba(0, 0, 0, 0.25);
    height: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .time-section {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
  }
  
  .time {
    margin: 0;
    font-size: 2.5rem;
  }