* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Dosis';
  letter-spacing: 2.5px;
}

@font-face {
  font-family: 'Dosis';
  src: local('Dosis'), url(./fonts/Dosis.ttf) format('truetype');
}

.content,
.mint, 
.wallet{
  display: flex;
  justify-content: center;
  padding-top: 2.5vh;
  padding-bottom: 2.5vh;
  font-size: 4rem;
  color: #000;
  animation: fadeIn linear 1s;
}

.home{
  background-image: url('images/home.png');
  background-size: 100vw;
  background-position: center center;
  background-attachment: fixed;
  background-repeat: no-repeat;
  height: 65vh;
  background-size: 80vh;
  animation: fadeIn linear 2s;
}

.intro{
  display: flex;
  flex-direction: column;
  height: -65vmin;
  padding-bottom: 10px;
  background-image: url('images/intro.jpeg');
  background-size: cover;
  background-position: center center;
  background-attachment: fixed;
  background-repeat: no-repeat;
  animation: fadeIn linear 1s;
} 
.what{
  display: flex;
  flex-direction: column;
  height: -30vmin;
  padding-bottom: 10px;
  animation: fadeIn linear 1s;
} 
.why{
  display: flex;
  flex-direction: column;
  height: -37.5vmin;
  padding-bottom: 10px;
  background-image: url('images/why.png');
  background-size: cover;
  background-position: center center;
  background-attachment: fixed;
  background-repeat: no-repeat;
  animation: fadeIn linear 1s;
} 
.who{
  display: flex;
  flex-direction: column;
  height: -75vmin;
  padding-bottom: 10px;
  animation: fadeIn linear 1s;
} 
.how{
  display: flex;
  flex-direction: column;
  height: -30vmin;
  padding-bottom: 10px;
  background-image: url('images/how.jpg');
  background-size: cover;
  background-position: center center;
  background-attachment: fixed;
  background-repeat: no-repeat;
  animation: fadeIn linear 1s;
}

.introH, 
.whatH, 
.whyH, 
.whoH, 
.howH{
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 10px;
  font-size: 4rem;
}

.introB,
.whatB, 
.whyB,
.whoB,
.howB{
  padding-top: 10px;
  padding-left: 7.5vw;
  padding-right: 7.5vw;
  text-align: center;
  font-size: 1.25rem;
  line-height: 1.75rem;
}

.link{
  outline: none;
  font-weight: bold;
  text-decoration: none;
  padding: 0px;
}

.content-items{
  display: flex;
  flex-direction: column;
  gap: 10vw;
  animation: fadeIn linear 2s;
  padding-bottom: 5vh;
}

.content-item{
  display: flex;
  justify-content: flex-start;
  padding-left: 5vw;
  gap: 10vw;
}

.video{
  width: 40vw;
  padding: 4px;
  border: 9px outset rgb(46, 43, 43);
  border-radius: 5px;
}

.metadata{
  font-size: 1.25rem;
  line-height: 4vw;
}

.mint{
  height: 65vh;
}

@keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

.fade-in-section {
  opacity: 0;
  transform: translateY(10vh);
  visibility: hidden;
  transition: opacity 0.4s ease-out, transform 1.2s ease-out;
  will-change: opacity, visibility;
}

.fade-in-section.is-visible {
  opacity: 1;
  transform: none;
  visibility: visible;
}